#Top-sellers * {
  box-sizing: content-box;
}
.top-sellers-bck {
  background-color: #fafafa;
}
.subscribe-section {
  background-color: #1e1823;
  position: relative;
  padding-top: 62px;
  padding-bottom: 62px;
  background-image: url('../images/subscribe.jpeg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  text-align: center;
}
.container {
  width: 100%;
  max-width: 365px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

.section-header {
  color: #111;
  text-align: center;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: -0.6px;
}

.section-header-span {
  color: #fd9222;
}

.sellers-paragraph-desktop {
  display: none;
}

.sellers-paragraph {
  max-width: 306px;
  line-height: 1.29;
  letter-spacing: -0.02em;
  font-size: 14px;
  color-scheme: #111;
}

.images-list {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.images-list-element {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.paragraph-button-div {
  display: flex;
  justify-content: center;
}

.buy-button {
  margin-top: 20px;
  width: 120px;
  padding: 12px 28px;
  background: #fd9222;
  border-radius: 100px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2;
  color: #ffffff;
  border: #fd9222;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.buy-button:hover,
.buy-button:focus {
  background-color: #fcf1e0;
  color: #fd9222;
}

@media screen and (min-width: 320px) {
  .sellers-paragraph-desktop {
    display: none;
  }
}

.image-3 {
  display: none;
}

@media (min-device-pixel-ratio: 2),
  (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .subscribe-section {
    background-image: url('../images/subscribe@x2.jpeg');
  }
  .imges-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }

  .images-list {
    flex-direction: row;
    gap: 16px;
  }

  .sellers-paragraph {
    display: none;
  }

  .paragraph-button-div {
    justify-content: space-between;
    margin-top: 32px;
  }

  .sellers-paragraph-desktop {
    max-width: 306px;
    display: block;
    line-height: 1.29;
    letter-spacing: -0.02em;
  }

  .buy-button {
    margin-top: 0px;
    width: 162px;
    height: 50px;
    padding: 14px 40px;
    font-size: 18px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    min-width: 1136px;
  }

  .image-3 {
    display: block;
  }
}
