.benefits {
  display: flex;
  min-width: 375px;
  padding-top: 20px;
  padding-right: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* flex-shrink: 0; */
  background: #1e1823;
  padding-left: 15px;
}

.benefits-list {
  display: flex;
  width: 335px;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  /* flex-shrink: 0; */
  list-style: none;
  padding-left: 20px;
}

.benefits-list-element-1 {
  display: flex;
  flex-wrap: wrap;
  width: 336px;
  padding-right: 28px;
  align-items: flex-start;
  /* flex-shrink: 0; */
}

.benefits-list-element-2 {
  display: flex;
  flex-wrap: wrap;
  width: 336px;
  height: 260px;
  padding-right: 28px;
  align-items: flex-start;
  /* flex-shrink: 0; */
}

.benefits-list-element-3 {
  display: flex;
  flex-wrap: wrap;
  width: 336px;
  padding-right: 28px;
  align-items: flex-start;
  /* flex-shrink: 0; */
}

.benefits-title {
  padding-top: 10px;
  color: #fff;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.benefits-paragraph {
  color: rgba(255, 255, 255, 0.7);
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: -0.28px;
  margin-top: 0;
}

.benefits-svg {
  display: flex;
  width: 32px;
  height: 32px;
  padding: 17px;
  justify-content: center;
  align-items: center;
  /* flex-shrink: 0; */
  border-radius: 365px;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.texts-div {
  display: flex;
  width: 308px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 18px;
  margin-bottom: 20px;
}

@media screen and (min-width: 1px) and (max-width: 1199px) {
  .linie-svg {
    display: none;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  /* Benefits section */

  .benefits {
    display: flex;
    min-width: 768px;

    /* padding: 50px 32px; */
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: 32px;
    padding-bottom: 0px;
    /* flex-shrink: 0; */
  }

  .benefits-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 768px;
    padding-top: 20px;
    padding-bottom: 20px;
    gap: 32px;
  }
  .benefits-list-element-1 {
    height: 240px;
    display: flex;
    width: 308px;
    flex-direction: row;
    align-items: flex-start;
    gap: 22px;
    /* flex-shrink: 0; */
  }

  .benefits-list-element-2 {
    display: flex;
    width: 308px;
    height: 243px;
    flex-direction: row;
    align-items: flex-start;
    gap: 22px;
    /* flex-shrink: 0; */
  }
  .benefits-list-element-3 {
    height: 240px;
    display: flex;
    width: 308px;
    flex-direction: row;
    align-items: flex-start;
    gap: 22px;
    /* flex-shrink: 0; */
  }
}
/* DESKTOP */
@media screen and (min-width: 1200px) {
  /* Advantages */
  .benefits {
    display: flex;
    min-width: 1250px;
    /* height: 344px; */
    padding: 50px;
    justify-content: center;
    align-items: center;
    /* flex-shrink: 0; */
  }

  .benefits-list {
    display: flex;
    flex-direction: row;
    min-width: 1250px;
    /* height: 244px; */
    justify-content: center;
    align-items: flex-start;
    gap: 70px;
    /* flex-shrink: 0; */
    padding: 0;
  }

  .benefits-list-element-1 {
    display: flex;
    /* height: 244px; */
    align-items: center;
    gap: 22px;
  }

  .benefits-list-element-2 {
    display: flex;
    /* height: 243px; */
    align-items: center;
    gap: 22px;
  }

  .benefits-list-element-3 {
    display: flex;
    /* height: 243px; */
    align-items: center;
    gap: 22px;
  }
}
