.subscribe-section {
  background-color: #1e1823;
  position: relative;
  padding-top: 62px;
  padding-bottom: 62px;
  background-image: url('../images/subscribe.jpeg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.subcribe-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 365px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.subscribe-title {
  font-weight: 600;
  font-size: 20px;
  letter-spacing: -0.03em;
  color: #ffffff;
  margin-bottom: 24px;
}

.subscribe-paragraph {
  letter-spacing: -0.02em;
  margin-bottom: 24px;
  color: #ffffff;
}

.subscribe-button {
  background-color: #fd9222;
  border-radius: 100px;
  width: 128px;
  height: 41px;
  font-weight: 600;
  color: #ffffff;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  font-size: 14px;
  border: none;
}

.subscribe-button:hover,
.subscribe-button:focus {
  background-color: #fcf1e0;
  color: #fd9222;
}

@media (min-device-pixel-ratio: 2),
  (-webkit-min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .subscribe-section {
    background-image: url('../images/subscribe@x2.jpeg');
  }
}

@media screen and (min-width: 768px) {
  .subscribe-container {
    max-width: 497px;
  }

  .subscribe-section {
    padding-top: 72px;
    padding-bottom: 72px;
  }

  .subscribe-paragraph {
    width: 497px;
  }

  .subscribe-title {
    font-size: 24px;
  }

  .subscribe-button {
    font-size: 18px;
  }
}
