body {
  font-family: 'Montserrat', sans-serif;
  font-family: 'Raleway', sans-serif;
  font-family: 'Roboto', sans-serif;
}

/* HOW IT'S MAKE? */
/* MOBILE */

div[id="how-it's-made"] *,
#Our-products *,
.container-all {
  box-sizing: content-box;
}

.container-all {
  margin: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 40px;
  background: #fafafa;
}

.title {
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
  line-height: 1.2;
  line-height: 24px;
  letter-spacing: -0.6px;
  text-transform: uppercase;
  text-align: center;
  color: #111111;
  margin-bottom: 32px;
}

.orange-name {
  color: #fd9222;
}

.flow-video {
  width: 100%;
  // width: 335px;
  // height: 500px;
  border-radius: 15px;
  padding-bottom: 20px;
}

.border {
  padding: 20px 14px 20px 14px;
  border-radius: 15px;
  background: #fff;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.container-paragraf {
  display: flex;
  gap: 18px;
  flex-direction: column;
}
.title-second {
  color: #111;
  font-size: 18px;
  font-weight: 600;
  line-height: 24px;
  margin-top: 20px;
}

.paragraf-section-three {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  line-height: 1.28571;
  letter-spacing: -0.02em;
  color: #111;
  margin: 0;
}

/* TABLETA */
@media screen and (width > 768px) {
  .container-all {
    margin: auto;
    padding-left: 77px;
    padding-right: 77px;
    padding-top: 20px;
    padding-bottom: 20px;
    background: #fafafa;
  }
  .title {
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: -0.72px;
    text-align: center;
    text-transform: uppercase;
    color: #111111;
  }
  .made-orange {
    color: #fd9222;
  }
  .flow-video {
    width: 100%;
    height: 400px;
    border-radius: 15px;
  }

  .flow-video {
    padding-bottom: 20px;
  }
  .border {
    padding: 32px;
    border-radius: 15px;
    background: #fff;
  }

  .container-paragraf {
    display: flex;
    gap: 18px;
    flex-direction: column;
  }
  .title-second {
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    color: #111111;
    margin-top: 20px;
  }
  .paragraf-section-three {
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.28px;
    color: #111111;
  }
}

/* DESKTOP */
@media screen and (width > 1200px) {
  .container-all {
    margin: auto;
    padding-left: 32px;
    padding-right: 32px;
    height: 478px;
    background: #fafafa;
    /*  width: 1136px; */
  }

  .title {
    text-align: center;
    font-family: Montserrat;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
    letter-spacing: -0.72px;
    text-transform: uppercase;
    color: #111111;
  }

  .desktop-position {
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: center;
  }

  .made-orange {
    color: #fd9222;
  }
  .flow-video {
    width: 600px;
    height: 400px;
    border-radius: 15px;
  }

  .border {
    width: 452px;
    height: 336px;
    border-radius: 15px;
    background: #fff;
    padding: 32px;
  }

  .container-paragraf {
    display: flex;
    gap: 24px;
    flex-direction: column;
  }

  .title-second {
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    color: #111111;
    width: 350px;
    margin-top: 0;
  }
  .paragraf-section-three {
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.28px;
    color: #111111;
    margin: 0;
  }
}

